import axios from 'axios';
import settings from '../settings';

export default class StaticService {
  static getTrainers() {
    const endPoint = process.env.NODE_ENV === "production" ? settings.apiEndpointGitHub : settings.apiEndpoint;

    return new Promise((resolve, reject) => {
      axios.get(endPoint + '/api/trainers.json')
        .then((responseObj) => {
          const response = responseObj.data || {};
          // if (response.success && response.items) {
            resolve(response.items);
          // } else {
          //   reject(response.error);
          // }
        })
        .catch((err) => {
          reject(err);
        });
    })
  }

  static getTrainersSets() {
    const endPoint = process.env.NODE_ENV === "production" ? settings.apiEndpointGitHub : settings.apiEndpoint;

    return new Promise((resolve, reject) => {
      axios.get(endPoint + '/api/sets.json')
        .then((responseObj) => {
          const response = responseObj.data || {};
          // if (response.success && response.items) {
          resolve(response.items);
          // } else {
          //   reject(response.error);
          // }
        })
        .catch((err) => {
          reject(err);
        });
    })
  }
}
