import { createRouter, createWebHistory } from 'vue-router';
import settings from "@/settings";

import AppNotFound from "@/modules/not-found/Not-found";
import AppMain from "@/modules/main/Main";
import AppAdmin from "@/modules/admin/Admin";
import AppModeration from "@/modules/moderation/Moderation";
import AppUser from "@/modules/user/User";
import AppTrainers from "@/modules/trainers/Trainers";
import AppTrainer from "@/modules/trainer/Trainer";
import AppClubs from "@/modules/clubs/Clubs";
import AppClub from "@/modules/club/Club";
import AppSets from "@/modules/sets/Sets";
import AppSet from "@/modules/set/Set";
import AppBuilders from "@/modules/builders/Builders";
import AppBuilder from "@/modules/builder/Builder";

const baseUrlPath = process.env.NODE_ENV === "production" ? settings.apiEndpointGitHub : settings.apiEndpoint;

const routes = [
  {
    path: `${baseUrlPath}/`,
    name: 'Home',
    component: AppMain
  },
  {
    path: `${baseUrlPath}/admin`,
    name: 'Admin',
    component: AppAdmin,
  },
  {
    path: `${baseUrlPath}/moderation`,
    name: 'Moderation',
    component: AppModeration
  },
  {
    path: `${baseUrlPath}/user`,
    name: 'User',
    component: AppUser
  },
  {
    path: `${baseUrlPath}/trainers`,
    name: 'Trainers',
    component: AppTrainers
  },
  {
    path: `${baseUrlPath}/trainer/:guid`,
    name: 'Trainer',
    component: AppTrainer
  },
  {
    path: `${baseUrlPath}/clubs`,
    name: 'Clubs',
    component: AppClubs
  },
  {
    path: `${baseUrlPath}/club`,
    name: 'Club',
    component: AppClub
  },
  {
    path: `${baseUrlPath}/sets`,
    name: 'Sets',
    component: AppSets
  },
  {
    path: `${baseUrlPath}/set/:guid`,
    name: 'Set',
    component: AppSet
  },
  {
    path: `${baseUrlPath}/builders`,
    name: 'Builders',
    component: AppBuilders
  },
  {
    path: `${baseUrlPath}/builder`,
    name: 'Builder',
    component: AppBuilder
  },
  {
    path: '/:pathMatch(.*)*',
    component: AppNotFound
  },
]

const router = createRouter({
  history: createWebHistory(),
  routes
})

export default router
