<template>
  <app-back></app-back>
  <app-sets-item :item="setInfo" v-if="setInfo"></app-sets-item>

  <app-set-item :sets="setInfo.sets" v-if="setInfo"></app-set-item>
</template>

<script>
import StaticService from "@/services/static";
import AppBack from "@/components/Back";
import AppSetItem from "@/components/SetItem";
import AppSetsItem from "@/components/SetsItem";

export default {
  name: 'AppSet',
  components: {
    AppSetsItem,
    AppSetItem,
    AppBack
  },
  data: () => ({
    setInfo: null
  }),
  computed: {
    trainersSets() {
      return this.$store.state.trainersSets;
    },
    setGuid() {
      return this.$route.params.guid;
    },
  },
  mounted() {
    this.getTrainersSets();
    // this.getSet();
    // this.getTrainers();
  },
  methods: {
    // getTrainers() {
    //   StaticService.getTrainers()
    //       .then(res => {
    //         this.$store.commit('setTrainersList', res);
    //         // this.trainerInfo = this.trainers.find((item) => item.guid === this.trainerGuid);
    //       })
    // },
    getTrainerSets() {
      StaticService.getTrainersSets()
          .then(res => {
            this.$store.commit('setTrainersSets', res);
            this.setInfo = this.trainersSets.find((item) => item.guid === this.setGuid);
          })
    },
    getTrainersSets() {
      if (this.trainersSets.length) {
        this.setInfo = this.trainersSets.find((item) => item.guid === this.setGuid);
      } else {
        this.getTrainerSets();
      }
    },
  }
}
</script>

