<template>
  <AppHeader></AppHeader>
  <main>
    <router-view/>
  </main>
  <AppFooter></AppFooter>
</template>

<script>
import StaticService from "@/services/static";

import AppHeader from "@/components/Header";
import AppFooter from "@/components/Footer";

export default {
  name: 'App',
  components: {
    AppHeader,
    AppFooter,
  },
  mounted() {
    this.getAllMockData();
  },
  methods: {
    getAllMockData() {
      StaticService.getTrainers()
          .then(res => {
            this.$store.commit('setTrainersList', res);
          })

      StaticService.getTrainersSets()
          .then(res => {
            this.$store.commit('setTrainersSets', res);
          })
    }
  }
}
</script>

