<template>
  <!--    - отображаем тренера, можно его фото, контакт, зал где можно его нанять <br>-->
  <!--    - отображаем список тренировок что создал тренер <br>-->
  <!--    - можно найти занятия по: залу/программе/тренажеру-->
  <app-back></app-back>

  <app-trainer-item v-if="trainerInfo" :item="trainerInfo"></app-trainer-item>

  <div v-if="!trainerSets.length">
    Пока без тренировок
  </div>

  <div v-else>
    <app-sets-item v-for="set in trainerSets" :key="set.index" :item="set"></app-sets-item>
  </div>
</template>

<script>
import StaticService from "@/services/static";

import AppBack from "@/components/Back";
import AppTrainerItem from "@/components/TrainerItem";
import AppSetsItem from "@/components/SetsItem";

export default {
  name: 'AppTrainer',
  components: {
    AppSetsItem,
    AppBack,
    AppTrainerItem
  },
  data: () => ({
    trainerInfo: null,
    trainerSets: []
  }),
  computed: {
    trainers() {
      return this.$store.state.trainersList;
    },
    trainersSets() {
      return this.$store.state.trainersSets;
    },
    trainerGuid() {
      return this.$route.params.guid;
    },
  },
  mounted() {
    this.getTrainer();
    this.getTrainersSets();
  },
  methods: {
    getTrainerSets() {
      StaticService.getTrainersSets()
          .then(res => {
            this.$store.commit('setTrainersSets', res);
            this.trainerSets = this.trainersSets.filter((item) => item.trainerGuid === this.trainerGuid);
          })
    },
    getTrainers() {
      StaticService.getTrainers()
          .then(res => {
            this.$store.commit('setTrainersList', res);
            this.trainerInfo = this.trainers.find((item) => item.guid === this.trainerGuid);
          })
    },
    getTrainersSets() {
      if (this.trainersSets.length) {
        this.trainerSets = this.trainersSets.filter((item) => item.trainerGuid === this.trainerGuid);
      } else {
        this.getTrainerSets();
      }
    },
    getTrainer() {
      if (this.trainers.length) {
        this.trainerInfo = this.trainers.find((item) => item.guid === this.trainerGuid);
      } else {
        this.getTrainers();
      }
    }
  }
}
</script>

